@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{
    .page{
        @apply w-full h-screen overflow-hidden;
    }
    .h1{
        @apply text-[35px] leading-tight md:text-[60px] md:leading-[1.3] mb-8 font-semibold;
    }
    .h2{
        @apply text-[35px] leading-tight md:text-[54px] md:leading-[1.3] mb-8 font-semibold;
    }
    .p{
        @apply leading-[1.8]  font-light;
    }
    .btn,
    .input{
        @apply h-[52px]
    }
  
      
      .moon-glow {
        
        width: 150px;
      
        position: absolute;
        height: 150px;
        border-radius: 50%;
        background-color: #f0e68c; /* Moon color */
        animation: glowing 3s infinite; /* Apply the glowing animation */
      }
      
      @keyframes glowing {
        0% {
          box-shadow: 0 0 5px #f0e68c, 0 0 10px #f0e68c, 0 0 20px #f0e68c; /* Glowing effect */
        }
        50% {
          box-shadow: 0 0 20px #f0e68c, 0 0 30px #f0e68c, 0 0 40px #f0e68c; /* Glowing effect */
        }
        100% {
          box-shadow: 0 0 5px #f0e68c, 0 0 10px #f0e68c, 0 0 20px #f0e68c; /* Glowing effect */
        }
      }
 
}